const textCenter = {display: 'flex', justifyContent: 'center', alignItems: 'center'};


class App extends React.Component {


  constructor(props) {
    super(props);
    this.state = { commandString: "HEY!", mustSpin: false, setMustSpin: false, "buttons": [] };

  }

  componentDidMount() {
    const state = {};
    const recoveryState = {};

    const initialize = (getState, getRecoveryState) => {
      if (process.env.NODE_ENV === 'development') {
          return createSmartappDebugger({ token: SBER_TOKEN, initPhrase: 'Запусти твистер для бокса', getState, getRecoveryState });
      }
      return createAssistant({ getState, getRecoveryState });
    };

    const assistant = initialize(() => state, () => recoveryState);

    assistant.on('start', () => {
      console.log('start');
      this.setState({ commandString: this.state.commandString.concat(' /// ', 'READY') });
    });

    const onDataFn = (command) => {
      console.log('command: '+JSON.stringify(command));
      this.setState({ commandString: this.state.commandString.concat(' /// ', JSON.stringify(command)) });

      if (command.smart_app_data && command.smart_app_data.test) {
        this.setState({ playerName: command.smart_app_data.player });
        this.setState({ prizeNumber: command.smart_app_data.test });
        this.setState({ mustSpin: true });

      }

      if (command.smart_app_data && command.smart_app_data.buttons) {
        this.setState({ buttons: command.smart_app_data.buttons });
      }

      if (command.navigation) {
        switch(command.navigation.command) {
          case 'UP':
            // window.scrollTo(0, 0);
            break;
          case 'DOWN':
            // window.scrollTo(0, 1000);
            break;
          default:
        }
      }
    }

    assistant.on('data', onDataFn);
    window.onDataFn = onDataFn;

  }



  render() {


    function ViewSuggestions(props){
      function handleClick(event) {
        console.log('click on', event);
        // assistant.sendData({ action: { action_id: event} });
      }
      var butList = null;
      if (typeof props.buttons === "object") {
        butList = props.buttons.map((item, index) =>
          <Button key={index} view="primary" size="s" id={'button_'+ item.payload} onClick={() => handleClick(item.payload)} text={item.title} />
        );
      }
      return (
        <div id="buttons">{butList}</div>
      );
    }

    const data = [
      { option: 'левая рука', style: { backgroundColor: '#D9303E' } },
      { option: 'левая рука', style: { backgroundColor: '#6156A6' } },
      { option: 'левая рука', style: { backgroundColor: '#038C73' } },
      { option: 'левая рука', style: { backgroundColor: '#F2C641' } },
      { option: 'правая рука', style: { backgroundColor: '#D9303E' } },
      { option: 'правая рука', style: { backgroundColor: '#6156A6' } },
      { option: 'правая рука', style: { backgroundColor: '#038C73' } },
      { option: 'правая рука', style: { backgroundColor: '#F2C641' } },
      { option: 'левая нога', style: { backgroundColor: '#D9303E' } },
      { option: 'левая нога', style: { backgroundColor: '#6156A6' } },
      { option: 'левая нога', style: { backgroundColor: '#038C73' } },
      { option: 'левая нога', style: { backgroundColor: '#F2C641' } },
      { option: 'правая нога', style: { backgroundColor: '#D9303E' } },
      { option: 'правая нога', style: { backgroundColor: '#6156A6' } },
      { option: 'правая нога', style: { backgroundColor: '#038C73' } },
      { option: 'правая нога', style: { backgroundColor: '#F2C641' } },
    ]

    return <div>
            <header className="player">{this.state.playerName}</header>
            <div style={textCenter}>
              <Sky
                images={{
                  0: twisterImage
                }}
                how={130}
                time={40}
                size={'100px'}
                background={'#038C73'}
              />
              <div className="wheel">
                <Wheel
                    mustStartSpinning={this.state.mustSpin}
                    prizeNumber={this.state.prizeNumber}
                    data={data}
                    outerBorderWidth={1}
                    outerBorderColor={['#ffffff']}
                    innerBorderColor={['#ffffff']}
                    innerBorderWidth={1}
                    radiusLineColor={['#ffffff']}
                    radiusLineWidth={1}
                    textColors={['#ffffff']}
                    innerRadius={15}
                    fontSize={20}

                    onStopSpinning={() => {
                        this.setState({ mustSpin: false });
                    }}
                />
              </div>
            </div>;
            <div>
              <ViewSuggestions buttons={this.state.buttons}/>
            </div>
           </div>;
  }
}

export default App;